html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}
.resetIcon {
  position: absolute;
  top: 3rem;
  right: 1rem;
}
.appIcon {
  /* position: absolute;
  top: 1.5rem;
  left: 3rem; */
  margin: 5px;
  margin-top: 35px;
  color: #feffff;
}
.App-header {
  background-color: #17252a;
  min-height: 15vh;
  display: flex;
  flex-direction: row;
  /* align-items: baseline; */
  justify-content: center;
  align-content: center;
  font-size: calc(10px + 2vmin);
  color: #feffff;
}

.headerParagrah {
  margin-left: 5px;
  margin-top: 45px;
}
.sweet-loading {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  /* left: 45%;
  top: 35%; */
  width: 100%;
  height: 100%;
  z-index: 998;
  opacity: 0.8;
}

@media (min-width: 768px) {
  .container-1 {
    display: flex;
    /*
    align-items:flex-start;
    align-items:flex-end;
    align-items:center;
    
    flex-direction:column;
    */
  }

  .container-2 {
    display: flex;
    flex-wrap: wrap;
    /*
    justify-content:flex-start;
    justify-content:flex-end;
    justify-content:center;
    justify-content:space-around;
    */
    justify-content: space-around;
  }
}

/* .container-3 {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
} */
.container-2 div {
  border: 1px #3aafa9 solid;
  background: #3aafa9;
  padding: 10px;
}

.container-2-box {
  /* flex-basis: 27%; */
  flex: 1 1 45%; /* or 1 0 100px, and no more need in min-width at all */
  max-width: 100%;
  min-width: 45%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  padding: 10px;
  margin: 10px;
  transition: 0.3s all ease;
}
.container-2-box:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  cursor: pointer;
  transform: scale(1.02);
  z-index: 999;
}
