.container-pbi-box {
  /* flex-basis: 27%; */
  flex: 1 1 20%; /* or 1 0 100px, and no more need in min-width at all */
  max-width: 100%;
  min-width: 20%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  padding: 10px;
  margin: 5px;
  position: relative;
  border-radius: 3px;
  transition: 0.3s all ease;
  color: white;
}
.container-pbi-box:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  background-color: #2b7a78;
  border: #2b7a78;
  cursor: pointer;
  transform: scale(1.05);
  z-index: 999;
}
.effort {
  position: absolute;
  top: 1rem;
  right: 2rem;
  background: #17252a;
  border: 1rem solid #2b7a78;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  color: #feffff;
}
