body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}
.resetIcon {
  position: absolute;
  top: 3rem;
  right: 1rem;
}
.appIcon {
  /* position: absolute;
  top: 1.5rem;
  left: 3rem; */
  margin: 5px;
  margin-top: 35px;
  color: #feffff;
}
.App-header {
  background-color: #17252a;
  min-height: 15vh;
  display: flex;
  flex-direction: row;
  /* align-items: baseline; */
  justify-content: center;
  align-content: center;
  font-size: calc(10px + 2vmin);
  color: #feffff;
}

.headerParagrah {
  margin-left: 5px;
  margin-top: 45px;
}
.sweet-loading {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  /* left: 45%;
  top: 35%; */
  width: 100%;
  height: 100%;
  z-index: 998;
  opacity: 0.8;
}

@media (min-width: 768px) {
  .container-1 {
    display: flex;
    /*
    align-items:flex-start;
    align-items:flex-end;
    align-items:center;
    
    flex-direction:column;
    */
  }

  .container-2 {
    display: flex;
    flex-wrap: wrap;
    /*
    justify-content:flex-start;
    justify-content:flex-end;
    justify-content:center;
    justify-content:space-around;
    */
    justify-content: space-around;
  }
}

/* .container-3 {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
} */
.container-2 div {
  border: 1px #3aafa9 solid;
  background: #3aafa9;
  padding: 10px;
}

.container-2-box {
  /* flex-basis: 27%; */
  flex: 1 1 45%; /* or 1 0 100px, and no more need in min-width at all */
  max-width: 100%;
  min-width: 45%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  padding: 10px;
  margin: 10px;
  transition: 0.3s all ease;
}
.container-2-box:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  cursor: pointer;
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  z-index: 999;
}


.teamIcon {
  position: absolute;
  top: 1rem;
  right: 4rem;
  color: #feffff;
}
.container-2-box {
  position: relative;
  display: flex;
  background: #3aafa9;
  border-radius: 5px;
}
.teamSection {
  text-align: left;
  color: #feffff;
}

.container-2-box {
  flex: 1 1 25%; /* or 1 0 100px, and no more need in min-width at all */
  max-width: 100%;
  min-width: 25%;
  position: relative;
  display: flex;
  background: #6cc563;
  border-radius: 5px;
}
.iterationIcon {
  position: absolute;
  top: 2rem;
  right: 4rem;
  color: #feffff;
}

.container-pbi-box {
  /* flex-basis: 27%; */
  flex: 1 1 20%; /* or 1 0 100px, and no more need in min-width at all */
  max-width: 100%;
  min-width: 20%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  padding: 10px;
  margin: 5px;
  position: relative;
  border-radius: 3px;
  transition: 0.3s all ease;
  color: white;
}
.container-pbi-box:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  background-color: #2b7a78;
  border: #2b7a78;
  cursor: pointer;
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  z-index: 999;
}
.effort {
  position: absolute;
  top: 1rem;
  right: 2rem;
  background: #17252a;
  border: 1rem solid #2b7a78;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  color: #feffff;
}

